.info_container {
    margin-top: 240px;
    background: #F2EDDD;
    position: relative;
    width: 100%;
    border-radius: 20px;
    height: 430px;
    overflow: hidden;
    display: flex;
}

.info_bird {
    padding-left: 20px;
}

.info_text {
    padding-top: 150px;
    padding-left: 100px;
    max-width: 630px;
    color: #282828;
    font-family: "FuturaPT-Book", Helvetica;
    font-size: 32px;
    font-weight: 400;
    z-index: 1;
}

.info_ink {
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 0;
}

@media (max-width: 767px) {
    .info_container {
        margin-top: 25px;
        height: 650px;
        width: 100%;
        flex-direction: column;
    }
    .info_bird {
        padding: 5%;
        width: 90%;
        height: -moz-fit-content;
        height: fit-content;
        margin: 0 auto;
    }
    .info_text {
        padding: initial;
        padding-left: 26px;
        padding-right: 26px;
        font-size: 24px;
        font-weight: 400;
        line-height: 30px;
    }
    .info_ink {
        bottom: -50px;
        right: -25px;
    }
}